<template>
    <div>
        <el-select ref="selectLabel" v-model="value" filterable :placeholder="placeholder" @visible-change="onVisible"
            @change="onChange">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"
                @click.native="selectLabel = item.label">
            </el-option>
        </el-select>
    </div>
</template>

<script>
export default {
    // v-modelvalue
    model: {
        prop: 'modelValue',
        event: 'valueChange'
    },
    props: {
        modelValue: Number,//ID
        placeholder: String,
        initSelectLabel: String
    },
    //modelValue
    watch: {
        modelValue: function (val) {
            this.value = val
        }
    },
    data() {
        return {
            selectLabel: this.initSelectLabel,
            options: [],
            value: ''
        }
    },
    methods: {
        handleData(target = []) {
            let data = []
            target.forEach((val, index) => {
                let { categoryId: value, categoryName: label } = val
                data[index] = { value, label }
            })
            // console.log(data)
            return data
        },
        //val
        onChange(val) {
            this.$emit('valueChange', val)
        },
        onVisible(visible) {
            //Label
            this.selectLabel = this.$refs.selectLabel.selected.label
            // console.log(this.selectLabel)
            //
            if (visible) {
                this.$api.category.get()
                    .then((response) => {
                        // console.log(response);
                        const res = response.data
                        if (res.code === 200) {
                            //
                            this.options = this.handleData(res.data)
                        } else {
                            this.$message.error(res.msg)
                        }
                    })
            } else {
                //initSelectLabel
                if (!this.selectLabel) {
                    this.selectLabel = this.initSelectLabel
                    // console.log(this.selectLabel)
                }
                //selectLabel
                this.$emit('onSelectLabel', this.selectLabel)
            }
        }

    }

}
</script>

<style></style>