<template>
    <div class="register_form">
        <h2></h2>
        <div class="text">
            
            <a href="#/login"></a>
        </div>
        <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="80px" style="width:90%">
            <el-form-item label="" :label-width="formLabelWidth" prop="username">
                <el-input v-model="ruleForm.username" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="password">
                <el-input v-model="ruleForm.password" placeholder="" show-password autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="repeatPassword">
                <el-input v-model="ruleForm.repeatPassword" placeholder="" show-password
                    autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="email">
                <el-input v-model="ruleForm.email" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
            <el-form-item label="" :label-width="formLabelWidth" prop="phone">
                <el-input v-model="ruleForm.phone" placeholder="" autocomplete="off"></el-input>
            </el-form-item>
        </el-form>
        <div>
            <el-button @click="resetForm('ruleForm')"></el-button>
            <el-button style="width: 228px;" type="primary" @click="submitForm('ruleForm')"></el-button>
        </div>
    </div>
</template>

<script>
export default {
    props: {
        dialogVisible: {
            type: Boolean,
            default: true
        }
    },
    data() {
        let validatePass2 = (rule, value, callback) => {
            if (value !== this.ruleForm.password) {
                callback(new Error('!'));
            } else {
                callback();
            }
        };
        return {
            ruleForm: {
                username: '',
                password: '',
                repeatPassword: '',
                phone: null,
                email: null,
            },
            formLabelWidth: '120px',
            rules: {
                username: [
                    { required: true, message: '', trigger: 'blur' },
                ],
                password: [
                    { required: true, message: '', trigger: 'blur' },
                    { min: 5, max: 15, message: ' 5  15 ', trigger: ['blur', 'change'] }
                ],
                repeatPassword: [
                    { required: true, message: '', trigger: 'blur' },
                    { validator: validatePass2, trigger: 'change' }
                ],
                email: [
                    { type: 'email', message: '', trigger: ['blur', 'change'] }
                ],
                phone: [
                    { min: 11, max: 11, message: "11", trigger: ['blur', 'change'] }
                ]

            }
        };
    },
    methods: {
        resetForm(formName) {
            this.$refs[formName].resetFields();
        },
        submitForm(formName) {
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    this.register()
                }
            })
        },
        register() {
            let { username, password, email, phone } = this.ruleForm
            let data = { username, password, email, phone }
            // console.log(data)
            this.$api.user.register(data)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success('')
                        this.$router.push('/login')
                    } else {
                        this.$message.error(res.msg)
                    }
                })
        }
    }
}

</script>

<style scoped>
.register_form {
    box-sizing: border-box;
    width: 480px;
    height: 550px;
    background-color: #ffffffdc;
    margin-top: 110px;
    margin-right: 139px;
    text-align: center;
    border-radius: 10px;
    padding: 50px 50px;
}


.register_form h2 {
    font-size: 36px;
    font-weight: 600;
    color: #000;
    text-align: left;
}

.register_form .text {
    font-size: 14px;
    margin-bottom: 35px;
    text-align: left;
    color: #9b9b9b;
}

.register_form .text a:hover {
    color: #409BFF;
}
</style>