<template>
    <div class="goods_search">
        <!--  -->
        <el-input v-model="keyword" clearable placeholder="" :style="{ width: inputWidth + 'px' }"></el-input>
        <!--  -->
        <el-button type="primary" @click="searchGoods" plain></el-button>
    </div>
</template>

<script>
export default {
    name: 'GoodsSearch',
    props: {
        inputWidth: {
            type: Number,
            default: 150
        }
    },
    data() {
        return {
            keyword: null,//
        }
    },
    methods: {
        // 
        searchGoods() {
            this.$emit('onSearch', this.keyword)
        },
    }

}
</script>

