<template>
    <div class="scroll_goods">
        <slot></slot>
        <ul class="list">
            <li v-for="item in goodsList" :key="item.productId" class="list-item">
                <goods-card :image="item.image" :product-name="item.productName" :price="item.price"
                    :category-name="item.categoryName" :origin="item.origin">
                </goods-card>
            </li>
        </ul>
        <div class="footer">
            <p v-if="loading">...</p>
            <p v-else></p>
        </div>
    </div>
</template>
  
<script>
import GoodsCard from './GoodsCard.vue'
export default {
    components: { GoodsCard },
    data() {
        return {
            isLock: false,     // ,
            loading: true,
            goodsList: [],     // 
            currentPage: 0,    // 
            total: 0,          // 
            pageSize: 8,       // 
            categoryId: null,  //ID
            list: [],          // 
            isLastPage: false  //
        }
    },
    computed: {
        noMore() {
            return this.isLastPage || this.goodsList.length > 40
        },
    },
    watch: {
        noMore: function (val) {
            if (val) {
                this.loading = false
            }
        }
    },
    created: function () {
        //
        this.requestGoodsData({})
    },
    mounted() {
        // 
        window.addEventListener("scroll", this.scrolling);

    },
    beforeDestroy() {
        window.removeEventListener("scroll", this.scrolling);
    },
    methods: {
        scrolling() {
            // 
            let scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
            //windowHeight
            let windowHeight = document.documentElement.clientHeight || document.body.clientHeight;
            //scrollHeight
            let scrollHeight = document.documentElement.scrollHeight || document.body.scrollHeight;
            // console.log(scrollTop, scrollHeight, windowHeight);   
            //
            if (!this.isLock && !this.noMore && scrollTop + windowHeight > scrollHeight * 0.9) {
                // 
                this.isLock = true
                // console.log(scrollTop, scrollHeight);
                this.requestNextPageData()
            }
        },
        // 
        handleCurrentChange(index) {
            this.requestGoodsData({ currentPage: index })
        },
        // 
        requestNextPageData() {
            if (!this.isLastPage) {
                this.handleCurrentChange(this.currentPage + 1)
            }
        },
        // 
        showCategoryGoods() {
            let categoryId = this.categoryId
            this.requestGoodsData({ categoryId })
        },
        // 
        showAllGoods() {
            // ID = null
            this.categoryId = null
            // 
            this.keyword = ''
            this.requestGoodsData({})
        },
        // 
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '') {
            console.log(currentPage, pageSize, keyword, categoryId)
            let params = {
                currentPage,
                pageSize,
                keyword,
                categoryId
            }
            this.$api.goods.get(params)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        // this.$message.success(successMsg)
                        let { list, total, isLastPage } = res.data
                        this.list = list
                        this.total = total
                        this.isLastPage = isLastPage
                        this.currentPage += 1
                        // 
                        this.goodsList = this.goodsList.concat(this.list)
                    } else {
                        this.$message.error(response.data.msg)
                    }
                    // 
                    this.isLock = false
                })
                .catch((error) => {
                    setTimeout(() => {
                        // 
                        this.isLock = false
                    }, 2000)
                })
        }
    }
}
</script>

<style scoped>
.scroll_goods {
    /* height: 800px; */
}

.list-item {
    display: inline-block;
}

.footer {
    margin: 20px 0px;
}

.scroll_goods p {
    text-align: center;
}
</style>