<template>
    <div class="goods_container">
        <!--  -->
        <el-row type="flex" justify="space-around" style="margin-bottom:10px">
            <!--  -->
            <el-col :span="5">
                <category-select placeholder="" v-model="categoryId"
                    style="display: inline-block; width: 120px;"></category-select>
                <el-button type="primary" plain :disabled="!categoryId" @click="showCategoryGoods"></el-button>
            </el-col>
            <el-col :span="5">
            </el-col>
            <!--  -->
            <el-col :span="10">
                <el-button plain @click="showAllGoods"></el-button>
            </el-col>
            <!--  -->
            <el-col :span="2">
                <!-- <el-button type="success" plain @click="addGoodsVisible = true"></el-button> -->
            </el-col>
        </el-row>
        <!--  -->
        <goods-show :list="list"></goods-show>
        <!--  -->
        <el-pagination :total="total" :page-size="pageSize" layout="total, jumper, prev, pager, next" background
            @current-change="handleCurrentChange">
        </el-pagination>
    </div>
</template>

<script>
import CategorySelect from '../category/CategorySelect.vue';
import GoodsSearch from './GoodsSearch.vue';
import GoodsSearchOne from './GoodsSearchOne.vue';
import GoodsShow from './GoodsShow.vue';

export default {
    name: 'MyGoods',
    components: {
        CategorySelect,
        GoodsShow,
        GoodsSearch,
        GoodsSearchOne
    },
    data() {
        return {
            addGoodsVisible: false,//
            currentPage: 1,//
            total: 0,//
            pageSize: 20,//
            categoryId: null,//ID
            list: [],//
        }
    },
    created: function () {
        //
        this.requestGoodsData({})
    },
    methods: {
        // 
        handleCurrentChange(index) {
            this.requestGoodsData({ currentPage: index })
        },
        // 
        searchGoods(keyword) {
            this.requestGoodsData({ keyword })
        },
        // 
        showCategoryGoods() {
            let categoryId = this.categoryId
            this.requestGoodsData({ categoryId })
        },
        // 
        showAllGoods() {
            // ID = null
            this.categoryId = null
            // 
            this.keyword = ''
            this.requestGoodsData({})
        },
        // 
        onAddSuccess(msg) {
            this.requestGoodsData({}, msg)
        },
        // 
        requestGoodsData({ currentPage = 1, pageSize = this.pageSize, keyword = null, categoryId = null }, successMsg = '') {
            // console.log(currentPage, pageSize, keyword, categoryId)
            let params = {
                currentPage,
                pageSize,
                keyword,
                categoryId
            }
            this.$api.goods.get(params)
                .then((response) => {
                    // console.log(response);
                    const res = response.data
                    if (res.code === 200) {
                        this.$message.success(successMsg)
                        let { list, total } = res.data
                        this.list = list
                        this.total = total
                    } else {
                        this.$message.error(response.data.msg)
                    }
                })
        },
    }
}
</script>

<style lang="less" scoped></style>